    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center
}

.loading__content{
    display: flex;
}
.loading__content h1{
    font-family: 'Open Sans', -apple-system, 'Segoe UI', sans-serif;
    font-size: 50px;
    font-weight: bold;
    color: hsl(0, 0%, 13%);
    box-sizing: border-box;
}

.loading__content .dots {
    display: inline-flex;
    position: relative;
    align-items: flex-end;
    padding-bottom: 15px;
}

.loading__content .dot{
    display: inline-block;
    width: 10px;
    height: 10px;
    background: hsl(0, 0%, 13%);
    border-radius: 10px;
    position: relative;
    margin-left: 6px;
}
.dot.z.animate{
    -webkit-animation: scale .8s .2s forwards;
            animation: scale .8s .2s forwards;
}
.dot.f.animate,.dot.s.animate{
    -webkit-animation: right .5s forwards;
            animation: right .5s forwards;
}
.dot.l.animate{
    -webkit-animation: rightDown .4s .1s forwards linear, drop 2s .4s forwards linear;
            animation: rightDown .4s .1s forwards linear, drop 2s .4s forwards linear;
}



.dot.z{
    position: absolute;
    -webkit-transform: scale(0);
            transform: scale(0);
    
}
@-webkit-keyframes scale {
    100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}}
@keyframes scale {
    100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}}

.dot.f,.dot.s{
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    
}

@-webkit-keyframes right {
    100% {
      -webkit-transform: translateX(16px);
              transform: translateX(16px);
    }
}

@keyframes right {
    100% {
      -webkit-transform: translateX(16px);
              transform: translateX(16px);
    }
}

.dot.t{
    background: transparent;

}

.dot.l{
    margin-left: 0;
    position: absolute;
    right: 0px;
    bottom: 15px;
   
}

@-webkit-keyframes rightDown {

    50% {
      bottom: 6px;
      right: -16px;
    }

    100% {
      bottom: -2px;
      right: -24px;
    }
  }

@keyframes rightDown {

    50% {
      bottom: 6px;
      right: -16px;
    }

    100% {
      bottom: -2px;
      right: -24px;
    }
  }

  @-webkit-keyframes drop {
    100% {
      -webkit-transform: translate(120px, calc(35px + (100vh/2)));
              transform: translate(120px, calc(35px + (100vh/2)));
    }
  }

  @keyframes drop {
    100% {
      -webkit-transform: translate(120px, calc(35px + (100vh/2)));
              transform: translate(120px, calc(35px + (100vh/2)));
    }
  }


